import React, {Component} from 'react';
import store from '../store';
import {connect} from 'react-redux';
// import UserBoards from '../components/UserBoards';
import NoteWrapper from '../components/NoteWrapper';
import {getBoardNotes, getBoard} from '../actions/board';
import {getAllNotes} from '../actions/note';

class UserBoardsContainer extends Component {

  componentDidMount() {
    this.props.onBoardEnter();

  }

  render() {
    console.log('props', this.props);
    const boardId = this.props.board.id;
    let boardIdNotes = [];
    if (this.props.notes.length) {
      boardIdNotes = this.props.notes.filter(note => {
        return boardId === note.board_id;
      });

    }
    console.log('notes', boardIdNotes);
    console.log('-------------------------');
    return (
      // <Board {...this.props} />
      <div style={{
        width   : 1000,
        height  : 1000,
        position: 'relative'}}>
        { this.props.notes.length ? (
            this.props.notes.map((note) => {
              const { left, top } = note;
              return (
                <NoteWrapper
                  key={note.id}
                  id={note.id}
                  left={left}
                  top={top}
                  note={note}
                />
              );
            })
          ) : null
        }
      </div>
    );
  }
}


const mapStateToProps = (state, ownProps) => {
  return {
    notes: state.noteReducer.all,
    board: ownProps.board
  };
};

const mapDispatchToProps = (dispatch, ownProps) => (
  {
    onBoardEnter: function() {
      const boardId = ownProps.board.id;
      // dispatch(getBoard(boardId));
      dispatch(getAllNotes({boardId}));
    }
  });

export default connect(mapStateToProps, mapDispatchToProps)(UserBoardsContainer);



